import ListMixin from '@/mixin/List.js'
export default {
  name: 'ticketCollection',
  mixins: [ListMixin],
  data() {
  	return {
  		loaded: false
  	}
  },
  methods: {
    getData () {
      this.$http(this.$urlPath.userFavoriteGoodsUrl, {
        page: this.page.num
      }, null, (data) => {
        this.loadSuccess(data.data)
      }, (errorCode, error) => {
        this.loadFail()
      }, () => {
		  this.loaded = true
		  uni.stopPullDownRefresh()
	  })
    },
    cancelCollection (index) {
		let item = this.list[index]
		uni.showModal({
			title: "提示",
			content: "是否要取消收藏？",
			confirmText: "取消",
			cancelText: "再想想",
			success: (res)=> {
				if (res.confirm) {
					this.$http(this.$urlPath.userUnFavoroteGoodsUrl, {
					goods_id: item.goods_id
				}, '正在操作…', (data) => {
					this.$toast('取消收藏成功')
					this.list.splice(index, 1)
				}, (errorCode, error) => {
					this.$toast(error)
				})
			}
		}
		})
    },
    ticketDetail (index) {
		let item = this.list[index]
		this.$push(this.$mRouter.ticketDetail({
			scenicId: item.scenicId,
			goodsId: item.goods_id
			// goodsSource: item.goods_source
		}))
    },
	onRefresh () {
		this.page.num = 1
		this.getData()
	}
  }
}
